.btn {
  background-color: var(--accent-color);
  color: var(--white-color);
  border-radius: 200px;
  font-size: 16px;
  line-height: 24px;
  font-weight: 500;

  transition: var(--transition);
}
.btn:hover,
.btn:focus {
  background-color: var(--dark-accent-color);
}

/* card button */
.cardBtn {
  composes: btn;
  padding: 16px 40px;
}

.filterBtn {
  composes: btn;
  padding: 16px 60px;
}

.formBtn {
  composes: btn;
  padding: 16px 60px;
}

/* load more */
.loadMoreBtn {
  composes: btn;
  padding: 16px 32px;
  background-color: var(--white-color);
  border: var(--border);
  color: var(--main-dark-color);
}
.loadMoreBtn:hover,
.loadMoreBtn:focus {
  color: var(--white-color);
  background-color: var(--dark-grey-color);
  border: 1px solid transparent;
}
